import { useState } from 'react';
import { API_BASE_URL } from './../constants/apiConstants';

const useEmailJs = () => {
  const [isSending, setIsSending] = useState(false);
  const [isSent, setIsSent] = useState(false);
  const [error, setError] = useState(null);

  // –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –º–∞—Ä—à—Ä—É—Ç–∞ –¥–ª—è –∫–∞–∂–¥–æ–≥–æ —Å—Ü–µ–Ω–∞—Ä–∏—è
  const determineRoute = (formData) => {
    if (formData.scene === 1) {
      return `${API_BASE_URL}/mailing/send-email/scene1`;
    } else if (formData.scene === 2) {
      return `${API_BASE_URL}/mailing/send-email/scene2`;
    } else if (formData.scene === 3) {
      return `${API_BASE_URL}/mailing/send-email/scene3`;
    }
    return null;
  };

  const sendEmail = async (formData) => {
    setIsSending(true);
    setIsSent(false);
    setError(null);
  
    try {
      const url = determineRoute(formData);
      if (!url) throw new Error('–ù–µ–∏–∑–≤–µ—Å—Ç–Ω—ã–π —Å—Ü–µ–Ω–∞—Ä–∏–π –æ—Ç–ø—Ä–∞–≤–∫–∏');
  
      const payload = {};
      if (formData.scene === 1) {
        payload.subject = formData.subject || '‚Äî';
        payload.phone = formData.phone || '‚Äî';
        payload.service = formData.service || '‚Äî';
        payload.inn = formData.inn || '‚Äî';
        payload.add_data = formData.add_data || '‚Äî';
        payload.file_data = formData.file_data || '‚Äî';
        payload.file_name = formData.file_name || '‚Äî';  // –¥–æ–±–∞–≤–ª—è–µ–º file_name
      } else if (formData.scene === 2) {
        payload.subject = formData.subject || '‚Äî';
        payload.name = formData.name || '‚Äî';
        payload.phone = formData.phone || '‚Äî';
      } else if (formData.scene === 3) {
        payload.subject = formData.subject || '‚Äî';
        payload.name = formData.name || '‚Äî';
        payload.recipient_email = formData.recipient_email || '‚Äî';
        payload.inn = formData.inn || '‚Äî';
        payload.message = formData.message || '‚Äî';
        payload.add_data = formData.add_data || '‚Äî';
        payload.file_data = formData.file_data || '‚Äî';
        payload.file_name = formData.file_name || '‚Äî';  // –¥–æ–±–∞–≤–ª—è–µ–º file_name
      }
      console.log(payload);
  
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });
      setIsSent(true);
  
      if (!response.ok) {
        throw new Error(`–û—à–∏–±–∫–∞: ${response.statusText}`);
      }
      
    } catch (err) {
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ email:', err);
      setError(err);
    } finally {
      setIsSending(false);
    }
  };
  

  return { sendEmail, isSending, isSent, error };
};

export default useEmailJs;
